import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Message } from 'primeng/primeng';

@Component({
  selector: 'customer-dialog',
  templateUrl: './customer-dialog.component.html',
  styleUrls: ['./customer-dialog.component.scss']
})
export class CustomerDialog implements OnInit {

  customerList = [];

  selectedList = [];

  custTypeList = [];

  @Output() outValue = new EventEmitter();

  @Output() closeDialog = new EventEmitter();

  msgs: Message[] = []; // 提示信息

  constructor() { }

  ngOnInit() {
    this.getCodeValues();
    this.getCustomerList();
  }

  getCodeValues() {
    this.custTypeList = [
      { label: '--请选择--', value: '' },
      { label: '借款人', value: '01' },
      { label: '共同借款人', value: '02' },
      { label: '担保人', value: '03' }
    ]; // 客户类型
  }

  getCustomerList() {
    this.customerList = [
      {
        custNo: '00000XFA945621',
        custType: '01',
        custName: '张三丰',
        globalType: '身份证',
        telphoneNo: '18999978458',
        acountNo: '57890101222222',
        globalId: '520121999925751'
      },
      {
        custNo: '00000XFA945622',
        custType: '02',
        custName: '李魁',
        globalType: '身份证',
        telphoneNo: '18999978458',
        acountNo: '57890101222222',
        globalId: '520121999925751'
      },
      {
        custNo: '00000XFA945623',
        custType: '03',
        custName: '张锐',
        globalType: '身份证',
        telphoneNo: '18999978458',
        acountNo: '57890101223212',
        globalId: '520121999925752'
      }
    ];
  }


  confirmChoose() {
    if (!this.selectedList || this.selectedList.length < 1) {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '请至少选择一条客户数据信息!' }];
    } else {
      this.outValue.emit(this.selectedList);
    }
  }
  // 关闭弹框
  back() {
    this.closeDialog.emit('close');
  }
}