import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Router } from '@angular/router';
import { CreditRrmService } from '../../http/credit-rrm.service';
import { StepBean } from '../bean/step.bean';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'step-add',
  templateUrl: './step-add.component.html',
  styleUrls: ['./step-add.component.scss']
})
export class StepAddComponent implements OnInit {

  addStage = new StepBean();

  @Input() unitNo: string; // 组件编号

  @Output() outValue = new EventEmitter();

  @Output() closeDialog = new EventEmitter();

  myform: FormGroup; //校验


  constructor(public commfunc: Commfunc, private managerService: CreditRrmService, public fb: FormBuilder) { }

  ngOnInit() {
    this.addStage.nodeId = this.unitNo;
    this.getDefaultValue();
    this.initValidateItems();
  }


  // 获取下拉框值
  getDefaultValue() {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;

    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let chName = JSON.parse(this.commfunc.getSessionDataCH("chName"));
    this.addStage.teller = commonHeader['userId'];
    this.addStage.org = chName.orgName;
  }

  // 初始化页面每个需要校验的字段的校验项(必输,正则匹配校验)
  initValidateItems() {
    this.myform = this.fb.group({
      "nodeId": new FormControl('', Validators.required),
      'stepName': new FormControl('', [Validators.required, Validators.maxLength(20)]),
      'remark': new FormControl('', [Validators.required, Validators.maxLength(60)]),
      'teller': new FormControl('', Validators.required),
      'org': new FormControl('', Validators.required)
    });

  }
  // 'stepName': new FormControl('', [Validators.pattern(/^[\w]{1,20}$/), Validators.required]),
  // 'remark': new FormControl('', [Validators.pattern(/^([\u4E00-\u9FA5\(\)\（\）]){1,60}$/), Validators.required]),
  // 'teller': new FormControl('', [Validators.pattern(/^[\w]{1,20}$/), Validators.required]),
  // 'org': new FormControl('', [Validators.pattern(/^[\w]{1,20}$/), Validators.required])

  saveStage() {
    this.managerService.createStep(this.addStage).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.outValue.emit('success');
      } else {
        this.outValue.emit(data.returnCode.message);
      }
    });
  }

  close() {
    this.closeDialog.emit('close');
  }
}
