import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';

import { Message } from 'primeng/primeng';

import { NodeBean } from '../bean/node.bean';
import { CreditRrmService } from '../../http/credit-rrm.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'node-add',
  templateUrl: './node-add.component.html',
  styleUrls: ['./node-add.component.scss']
})

export class NodeAddComponent implements OnInit {

  @Output() closeDialog = new EventEmitter();

  @Output() addSuccess = new EventEmitter();

  nodeBean: NodeBean = new NodeBean();

  showUnitList = false;

  msgs: Message[] = [];

  ruleTypeList = [];

  constructor(private managerService: CreditRrmService, public fb: FormBuilder, public commfunc: Commfunc) { }

  myform: FormGroup; //校验

  ngOnInit() {
    this.getCodeValues();
    this.initValidateItems();
  }

  // 获取下拉框值
  getCodeValues() {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    this.ruleTypeList = codeValues['eidtNodeType'];

    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let chName = JSON.parse(this.commfunc.getSessionDataCH("chName"));
    this.nodeBean.teller = commonHeader['userId'];
    this.nodeBean.org = chName.orgName;

  }

  // 初始化页面每个需要校验的字段的校验项(必输,正则匹配校验)
  initValidateItems() {
    this.myform = this.fb.group({
      'ruleType': new FormControl('', [Validators.required]),
      'ruleName': new FormControl('', [Validators.required, Validators.maxLength(20)]),
      'remark': new FormControl('', [Validators.required, Validators.maxLength(60)]),
      'teller': new FormControl('', [Validators.required]),
      'org': new FormControl('', [Validators.required])
    });
  }

  // 提交之前做一次校验
  preValidateForSubmit() {
    //校验的
    for (const i in this.myform.controls) this.myform.controls[i].markAsDirty();
    //处理是否提交
    for (const i in this.myform.controls) {
      if (!this.myform.controls[i].valid) return false;
    }
    return true;
  }

  // 保存处理
  save() {
    if (!this.preValidateForSubmit()) return;
    this.managerService.createRule(this.nodeBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.addSuccess.emit('success');
      } else {
        this.addSuccess.emit(data.returnCode.message);
      }
    });
  }

  back() {
    this.closeDialog.emit();
  }

  // 选择功能
  confirmChooseUnit(selectUnit) {
    if (!selectUnit) {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '请选择一条功能数据!' }];
      return;
    }
    this.nodeBean.nodeId = selectUnit.nodeId;
    this.nodeBean.nodeName = selectUnit.nodeName;
    this.showUnitList = false;
  }
}