import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';
import { CreditRrmService } from '../../http/credit-rrm.service';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'rule-design',
  templateUrl: './rule-design.component.html',
  styleUrls: ['./rule-design.component.scss']
})
export class RuleDesignComponent implements OnInit, OnDestroy {


  editUrl: any;

  inputParam: any;

  // tslint:disable-next-line:max-line-length
  constructor(
    private router: Router,
    private sanitizer: DomSanitizer,
    private routInfo: ActivatedRoute,
    private managerService: CreditRrmService) {

  }

  ngOnInit() {
    this.routInfo.params.subscribe((params: Params) => {
      this.inputParam = JSON.parse(params['flowParam']);
      let flowKey = encodeURI(this.inputParam.flowKey);//转换地址中中文名格式
      this.editUrl = this.sanitizer.bypassSecurityTrustResourceUrl(flowKey);
    });
  }

  ngOnDestroy(): void {
    const param = { stepId: this.inputParam.stepId, flowId: this.inputParam.flowId };
    // 刷新知识包
    this.managerService.refreshKnowledgeCacheByStepAndFlow(param).subscribe(data => {
    });
  }

  back() {
    this.router.navigate(['/pages/tzb/custom/credit-rrm/flowList']);
  }

}
