import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { CreditRrmService } from '../../http/credit-rrm.service';
import { StepBean } from '../bean/step.bean';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'step-update',
  templateUrl: './step-update.component.html',
  styleUrls: ['./step-update.component.scss']
})
export class StepUpdateComponent implements OnInit {


  editStep: StepBean;

  @Input() inputStep: any; // 传入选中的阶段对象进行编辑

  @Output() outValue = new EventEmitter();

  @Output() closeDialog = new EventEmitter();

  myform: FormGroup; //校验


  constructor(private managerService: CreditRrmService, public fb: FormBuilder) { }

  ngOnInit() {
    this.editStep = this.inputStep;
    this.initValidateItems();
  }



  // 初始化页面每个需要校验的字段的校验项(必输,正则匹配校验)
  initValidateItems() {
    this.myform = this.fb.group({
      'stepName': new FormControl('', [Validators.required, Validators.maxLength(20)]),
      'remark': new FormControl('', [Validators.required, Validators.maxLength(60)]),
    });
  }
  // 'stepName': new FormControl('', [Validators.pattern(/^[\w]{1,20}$/), Validators.required]),

  // 保存阶段
  saveStep() {
    this.managerService.updateStep(this.editStep).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.outValue.emit('success');
      } else {
        this.outValue.emit(data.returnCode.message);
      }
    });
  }

  close() {
    this.closeDialog.emit('close');
  }


}
