import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { CreditRrmService } from '../../http/credit-rrm.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'unit-maintenance',
  templateUrl: './unit-maintenance.component.html',
  styleUrls: ['./unit-maintenance.component.scss']
})
export class UnitMaintenanceComponent implements OnInit {


  selectUnit: any;

  unitTypeList = [];  // 组件类型

  showStepPanel = false; // 阶段维护panel

  showUnitPanel = true; // 组件维护panel

  constructor(
    public commfunc: Commfunc,
    private router: Router,
    private managerService: CreditRrmService,
    private routInfo: ActivatedRoute
  ) {
    this.routInfo.params.subscribe((params: Params) => {
      this.selectUnit = JSON.parse(params['selectNode']);
      if (params['showPanelIndex'] === 'showUnit') {
        this.showStepPanel = false;
        this.showUnitPanel = true;
      } else {
        this.showStepPanel = true;
        this.showUnitPanel = false;
      }
    });
  }

  ngOnInit() {
    this.getCodeValues();
  }

   // 获取下拉框值
   getCodeValues() {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    this.unitTypeList = codeValues['unitType'];
  }

  back() {
    this.router.navigate(['/pages/tzb/custom/credit-rrm/unit']);
  }
}
