import { Component, OnInit } from '@angular/core';
import { Message } from 'primeng/primeng';

import * as _ from 'lodash';

import { CheckQualificationBean, CheckQualifactionBean } from '../bean/check-qualification-bean';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { CreditRrmService } from '../../http/credit-rrm.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'check-qualification',
  templateUrl: './check-qualification.component.html',
  styleUrls: ['./check-qualification.component.scss']
})
// tslint:disable-next-line:component-class-suffix
export class CheckQualification implements OnInit {


  msgs: Message[] = [];

  checkParam = new CheckQualificationBean();

  stageTypeList = []; // 阶段类型编码

  businessLineList = []; // 业务种类

  triggerTypeList = []; // 触发类型

  identityIsTrueList = []; // 是否身份验真

  custTypeList = []; // 客户类型

  isPassList = []; // 是否通过

  myform: FormGroup; // 校验

  showCustomerDialog = false;

  customerNames = '';

  outputResult = new CheckQualifactionBean();

  constructor(
    public fb: FormBuilder,
    public commfunc: Commfunc,
    private managerService: CreditRrmService
  ) { }

  ngOnInit() {
    this.getCodeValues();
    this.initValidateItems();
  }

  query() {
    if (!this.preValidateForSubmit()) return;
    this.managerService.checkQualification(this.checkParam).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.outputResult.dateTime = data.dateTime;
        this.outputResult.result = data.result;
        this.outputResult.lastScore = data.lastScore;
        this.outputResult.sysAdvice = data.sysAdvice;
        this.outputResult.riskCode = data.riskCode;
        this.outputResult.investType = data.investType;
        this.outputResult.reportType = data.reportType;
        this.outputResult.lossLevel = data.lossLevel;
        this.outputResult.loanAdvice = data.loanAdvice;
        this.outputResult.loanRate = data.loanRate;
        this.outputResult.evalArray = data.evalArray;
      } else {
        this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
      }
    });
  }
  reset() {
    this.customerNames = '';
    this.checkParam = new CheckQualificationBean();
  }

  getCustomerList(result) {
    this.customerNames = '';
    this.checkParam.userInfoArray = result;
    for (const item of this.checkParam.userInfoArray) {
      this.customerNames = this.customerNames + item.custName + ','
    }
    this.customerNames = this.customerNames.substring(0, this.customerNames.length - 1);
    this.showCustomerDialog = false;
  }


  // 初始化页面每个需要校验的字段的校验项(必输,正则匹配校验)
  initValidateItems() {
    this.myform = this.fb.group({
      'processId': new FormControl('', Validators.required),
      'tranSeqNo': new FormControl('', Validators.required),
      'stageType': new FormControl('', Validators.required),
      'sourceSystem': new FormControl('', Validators.required),
      'triggerType': new FormControl('', Validators.required),
      'identityIsTrue': new FormControl('', Validators.required),
      'org': new FormControl('', Validators.required),
      'teller': new FormControl('', Validators.required),
      'customerNames': new FormControl('', Validators.required),
    });
  }

  // 提交之前做一次校验
  preValidateForSubmit() {
    for (const i in this.myform.controls) this.myform.controls[i].markAsDirty();
    // 处理是否提交
    for (const i in this.myform.controls) {
      if (!this.myform.controls[i].valid) return false;
    }
    return true;
  }

  getCodeValues() {
    this.stageTypeList = [
      { label: '--请选择--', value: '' },
      { label: '准入筛选', value: '01' },
      { label: '预筛选', value: '02' },
      { label: '信审评分', value: '03' }
    ]; // 阶段类型编码

    this.triggerTypeList = [
      { label: '--请选择--', value: '' },
      { label: '手工触发', value: '01' },
      { label: '系统触发', value: '02' }
    ]; // 触发类型

    this.identityIsTrueList = [
      { label: '--请选择--', value: '' },
      { label: '是', value: '0' },
      { label: '否', value: '1' }
    ]; // 是否身份验真

    this.custTypeList = [
      { label: '--请选择--', value: '' },
      { label: '借款人', value: '01' },
      { label: '共同借款人', value: '02' },
      { label: '担保人', value: '03' }
    ]; // 客户类型

    this.isPassList = [
      { label: '--请选择--', value: '' },
      { label: '通过', value: '0' },
      { label: '不通过', value: '1' }
    ]; // 是否通过

    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    this.businessLineList = codeValues['businessTypes'];
  }

}
