import { Component, OnInit } from '@angular/core';

import { SelectItem, Message } from 'primeng/primeng';

import { CreditRrmService } from '../../http/credit-rrm.service';
import { CreditRrmPaginatorBean } from '../../bean/credit-rrm-paginator-bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'app-operate-log',
  templateUrl: './operate-log.component.html',
  styleUrls: ['./operate-log.component.scss']
})

export class OperateLogComponent implements OnInit {

  queryParam = {
    operateType: '',
    operateModule: '',
    pageSize: 10,
    pageNum: 1
  };

  operateTypes: SelectItem[];

  operateModules: SelectItem[];

  msgs: Message[] = [];

  paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象

  operateTypeList = [];

  operateModulesList = [];

  successList = [];

  requestMsgContent: string;

  showRequestMsgDialog = false;

  // 构造器
  constructor(public commfunc: Commfunc, private managerService: CreditRrmService) { }

  // 初始化
  ngOnInit() {
    this.getCodeValues();
    this.query(false);
  }

  // 获取下拉框值
  getCodeValues() {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    this.operateTypeList = codeValues['operateType'];
    this.operateModulesList = codeValues['operateModule'];
    this.successList = codeValues['operateResult'];
  }

  query(nextPage) {
    if (!nextPage) {
      this.paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象
    }
    this.queryParam.pageNum = this.paginatorBean.currentPage;
    this.queryParam.pageSize = this.paginatorBean.pageSize;
    this.managerService.queryOperateLog(this.queryParam).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.paginatorBean.queryList = data.list;
        this.paginatorBean.resultCounts = data.totalNum;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '失败', detail: data.returnCode.message });
      }
    });
  }

  // 重置
  reset() {
    this.queryParam = {
      operateType: '',
      operateModule: '',
      pageSize: 10,
      pageNum: 1
    };
  }

  // 分页按钮事件
  paginate(event) {
    // tslint:disable-next-line:radix
    this.paginatorBean.currentPage = event.page + 1;
    this.paginatorBean.pageSize = event.rows;
    this.paginatorBean.first = event.page * this.paginatorBean.pageSize;
    this.query(true);
  }

  // 显示详情
  showDetailDialog(item) {
    this.requestMsgContent = item.request;
    console.log(this.requestMsgContent);
    this.showRequestMsgDialog = true;
  }
}