import { Component, OnInit } from '@angular/core';
import { SelectItem, Message } from 'primeng/primeng';
import { CreditRrmService } from '../../http/credit-rrm.service';
import { CreditRrmPaginatorBean } from '../../bean/credit-rrm-paginator-bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { QualifactionreviewBean } from '../bean/check-qualification-bean';
import { TZB_HTTP_CUS1 } from '../../../../../../@uisftech/common/service/http.constant';
import { API } from 'app/pages/tzb/custom/http/custom-view/custom-view.constant';
import * as $ from 'jquery';
import { CusViewHttpService } from '../../../http/custom-view/custom-view.http.service';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';

@Component({
  selector: 'query-qualification-review',
  templateUrl: './query-qualification-review.component.html',
  styleUrls: ['./query-qualification-review.component.scss']
})

export class QueryQualificationReview implements OnInit {
  //BI报告
  TZB_HTTP_CUS1 = TZB_HTTP_CUS1;
  userId: string;
  inValueReportId: string;
  reportCustNm: string;
  reportDisplay: boolean = false;
  progress: boolean = false;
  certType: string;
  certID: string;

  tranSeqNos: any;
  AssociatesTypeLoans: any;
  CustomGroupIdenTypes: any;
  GuaranteeModes: any;
  creditResults: any;
  isNames: any;
  truthResults: any;
  sourceSystems: any;
  decisionTypeCode: any;
  headerTitle: string = "资质审查结果详情";
  display: boolean = false;
  // 查询列表参数
  queryParam = {
    custNo: '',
    custName: '',
    advocateManagePerson: '',
    tellerOrg: '',
    startDate: '',
    endDate: '',
    pageSize: '1',
    pageNum: '10'
  };

  deployTimeStart: Date = null;
  deployTimeEnd: Date = null;

  ch: any = CALENDAR_CN;

  msgs: Message[] = [];

  paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象
  QualifactionreviewBean = new QualifactionreviewBean(); // 详情实体对象

  successList = [];

  requestMsgContent: string;

  showRequestMsgDialog = false;

  // 构造器
  constructor(public commfunc: Commfunc,
    private managerService: CreditRrmService,
    public httpServices: CusViewHttpService,
    public httpService: CusOperationHttpService, ) { }

  // 初始化
  ngOnInit() {
    this.getCodeValues();
  }

  // 获取下拉框值
  getCodeValues() {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    this.sourceSystems = codeValues['sourceSystems'];
    this.decisionTypeCode = codeValues['decisionTypeCode'];
    console.log(this.decisionTypeCode)
    this.truthResults = codeValues['truthResults'];
    this.isNames = codeValues['isNames'];
    this.creditResults = codeValues['creditResults'];
    this.GuaranteeModes = codeValues['CardGuaranteeMode'];
    this.CustomGroupIdenTypes = codeValues['CustomGroupIdenType'];
    this.AssociatesTypeLoans = codeValues['AssociatesTypeLoan'];
    this.tranSeqNos = codeValues['tranSeqNos'];
  }

  query(nextPage) {
    if (!nextPage) {
      this.paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象
    }
    this.queryParam.startDate = this.commfunc.transDateN(this.deployTimeStart);
    this.queryParam.endDate = this.commfunc.transDateN(this.deployTimeEnd);
    this.queryParam.pageNum = this.paginatorBean.currentPage.toString();
    this.queryParam.pageSize = this.paginatorBean.pageSize.toString();
    this.managerService.queryQualificationReviewInfoList(this.queryParam).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.paginatorBean.queryList = data.list;
        this.paginatorBean.resultCounts = data.totalNum;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '失败', detail: data.returnCode.message });
      }
    });
  }

  // 重置
  reset() {
    this.deployTimeStart = null;
    this.deployTimeEnd = null;
    this.queryParam = {
      custNo: '',
      custName: '',
      advocateManagePerson: '',
      tellerOrg: '',
      startDate: '',
      endDate: '',
      pageSize: '1',
      pageNum: '10'
    };
  }
  // 分页按钮事件
  paginate(event) {
    this.paginatorBean.currentPage = event.page + 1;
    this.paginatorBean.pageSize = event.rows;
    this.paginatorBean.first = event.page * this.paginatorBean.pageSize;
    this.query(true);
  }

  //详情界面
  reviewDetails(car) {
    this.display = true;
    this.QualifactionreviewBean = car;
  }
  //关闭
  closeClick() {
    this.display = false;
  }

  //查看BI报告
  toBi(item) {
    this.queryCard(item)
    this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
    let param = { custNo: item.custNo }
    let params = this.commfunc.handleParams(param)
    let TZB_HTTP_CUS1 = this.TZB_HTTP_CUS1 + API.creditReportCheck;
    let _self = this;
    $.ajax({
      url: TZB_HTTP_CUS1,
      type: 'POST',
      data: JSON.stringify(params),
      async: false,
      headers: {
        'x-ajax': '1',
        'Content-Type': 'application/json'
      },
      dataType: 'json',
      success: function (data) {
        if (data.returnCode.code == 'success') {
          if (data.isCreate == "1") {
            let userName = JSON.parse(_self.commfunc.getSessionDataCH('chName')).userName;
            _self.creatMainMsg({ custNo: item.custNo, modNum: data.updatedNum, custName: item.custName, qryUserId: _self.userId, cretType: _self.certType, cretNo: _self.certID, qryUserName: userName, crdtReportNo: data.crdtReportNo, accessWay: '1' })
          } else {
            _self.inValueReportId = data.reportId;
            _self.reportCustNm = item.custNo;
            _self.reportDisplay = true;
            _self.msgs = [];
            // _self.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
            _self.msgs.push({ severity: 'success', summary: 'Success Message', detail: data.returnCode.message });
          }
        } else {
          _self.msgs = [];
          _self.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
        }
      },
      error: function (err) {
        ////console.log(err);
      }
    })

  }
  creatMainMsg(param) {
    this.progress = true;
    let params = this.commfunc.handleParams(param);
    this.httpServices.createReportMainInfo(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.inValueReportId = data.reportId;
        this.reportCustNm = param.custNo;
        this.progress = false;
        this.reportDisplay = true;
      } else {
        this.progress = false;
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
      }
    }, err => {
      this.progress = false;
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
    })
  }
  queryCard(item) {
    this.httpService.selectCustBriefInfo({ custNo: item.custNo }).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.certType = data.idenType;
        this.certID = data.idenNum;
      }
    });
  }
}