import { Component, OnInit } from '@angular/core';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Router } from '@angular/router';
import { CreditRrmPaginatorBean } from '../../bean/credit-rrm-paginator-bean';
import { CreditRrmService } from '../../http/credit-rrm.service';
import { UnitQueryBean } from './bean/unit.query.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'unit-list',
  templateUrl: './unit-list.component.html',
  styleUrls: ['./unit-list.component.scss']
})
export class UnitListComponent implements OnInit {

  unitType = [];  // 组件类型

  paramsBean = new UnitQueryBean(); // 查询条件

  paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象

  msgs: Message[] = []; // 提示信息

  showAddPage = false; // 是否显示新增页

  constructor(
    private router: Router,
    public commfunc: Commfunc,
    private managerService: CreditRrmService,
    private confirmationService: ConfirmationService
  ) { }

  ngOnInit() {
    this.getCodeValues();
    this.query(false);
  }

  // 获取下拉框值
  getCodeValues() {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    this.unitType = codeValues['unitType'];
  }

  // 查询
  query(nextPage) {
    if (!nextPage) {
      this.paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象
    }
    this.paramsBean.nodeType = '0';
    this.paramsBean.pageNum = this.paginatorBean.currentPage;
    this.paramsBean.pageSize = this.paginatorBean.pageSize;
    this.managerService.queryNodeList(this.paramsBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.paginatorBean.queryList = data.list;
        this.paginatorBean.resultCounts = data.totalNum;
      } else {
        this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
      }
    });
  }

  // 重置查询条件
  reset() {
    this.paramsBean = new UnitQueryBean();
  }

  // 分页按钮事件
  paginate(event) {
    this.paginatorBean.currentPage = event.page + 1;
    this.paginatorBean.pageSize = event.rows;
    this.paginatorBean.first = event.page * this.paginatorBean.pageSize;
    this.query(true);
  }

  // 删除
  delete() {
    if (this.paginatorBean.selectBean && this.paginatorBean.selectBean.length > 0) {
      this.confirmationService.confirm({
        message: '确定要删除这些数据吗？',
        accept: () => {
          let nodeIds = '';
          let nodeNames = '';
          this.paginatorBean.selectBean.forEach(element => {
            nodeIds += element.nodeId + ',';
            nodeNames += element.nodeName + ',';
          });
          nodeIds = nodeIds.substr(0, nodeIds.length - 1);
          nodeNames = nodeNames.substr(0, nodeNames.length - 1);
          const unitNodeIdJson = {
            nodeId: nodeIds,
            nodeName: nodeNames
          };
          // 调用组件删除接口
          this.managerService.deleteNode(unitNodeIdJson).subscribe(data => {
            if (data.returnCode.code == 'success') {
              this.msgs = [{ severity: 'success', summary: '成功', detail: '删除成功！' }];
              this.query(false);
            } else {
              this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
              return;
            }
          });
        },
        reject: () => {
          this.msgs = [{ severity: 'info', summary: '删除', detail: '你选择了拒绝删除！' }];
        }
      });
    } else {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '至少选择一条数据!' }];
      return;
    }

  }

  // 功能维护
  toUnitMange() {
    if (this.paginatorBean.selectBean && this.paginatorBean.selectBean.length === 1) {
      this.router.navigate(['/pages/tzb/custom/credit-rrm/unit-maintenance', JSON.stringify(this.paginatorBean.selectBean[0]), 'showUnit']);
    } else {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '只能选择一条数据!' }];
    }
  }

  // 新增成功回调函数
  addCallBack(msg) {
    if (msg === 'success') {
      this.msgs = [{ severity: 'success', summary: '成功', detail: '新增成功!' }];
    } else {
      this.msgs = [{ severity: 'error', summary: '失败', detail: msg }];
    }
    this.showAddPage = false;
    this.query(false);
  }

}
