import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { Message, ConfirmationService } from 'primeng/primeng';

import { CreditRrmService } from '../../http/credit-rrm.service';
import { CreditRrmPaginatorBean } from '../../bean/credit-rrm-paginator-bean';
import { QueryFlowBean } from '../bean/query.bean';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'flow-list',
  templateUrl: './flow-list.component.html',
  styleUrls: ['./flow-list.component.scss']
})

export class FlowListComponent implements OnInit {

  inputStepId: any;

  qureyParam = new QueryFlowBean();
  paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象

  msgs: Message[] = [];

  showAddFlowPage = false;

  showFlowDetailPage = false;

  unitBean: any;

  flowPath: string;

  // 构造器
  constructor(
    private router: Router,
    private routInfo: ActivatedRoute,
    private confirmationService: ConfirmationService,
    private managerService: CreditRrmService
  ) { }

  // 初始化
  ngOnInit() {
    this.queryList(false);
  }

  // 查询流程列表
  queryList(nextPage) {
    if (!nextPage) {
      this.paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象
    }
    this.qureyParam.pageNum = this.paginatorBean.currentPage;
    this.qureyParam.pageSize = this.paginatorBean.pageSize;
    this.managerService.queryFlowList(this.qureyParam).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.paginatorBean.queryList = data.list;
        this.paginatorBean.resultCounts = data.totalNum;
      } else {
        this.msgs = [{ severity: 'error', summary: '失败', detail: data.returnCode.message }];
      }
    });
  }

  reset() {
    this.qureyParam = new QueryFlowBean();
  }

  // 分页按钮事件
  paginate(event) {
    // tslint:disable-next-line:radix
    this.paginatorBean.currentPage = event.page + 1;
    this.paginatorBean.pageSize = event.rows;
    this.paginatorBean.first = event.page * this.paginatorBean.pageSize;
    this.queryList(true);
  }

  // 新增回调函数
  addFlowCallBack(msg) {
    if (msg === 'success') {
      this.msgs = [{ severity: 'success', summary: '成功', detail: '新增程成功!' }];
    } else {
      this.msgs = [{ severity: 'error', summary: '失败', detail: msg }];
    }
    this.showAddFlowPage = false;
    this.queryList(false);
  }

  // 查看详情
  queryFlowDetail() {
    const selectBean = this.paginatorBean.selectBean;
    if (!selectBean) {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '请选择一条查询记录!' }];
      return;
    }
    this.flowPath = selectBean.flowPath;
    this.showFlowDetailPage = true;
  }

  // 规则编排
  ruleEdit() {
    const selectBean = this.paginatorBean.selectBean;
    if (!selectBean) {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '请选择一条查询记录!' }];
      return;
    }
    const param = { stepId: selectBean.stepId, flowId: selectBean.flowId, flowKey: selectBean.key };
    this.router.navigate(['/pages/tzb/custom/credit-rrm/ruleDesign', JSON.stringify(param)]);
  }

  // 删除
  delete() {
    if (this.paginatorBean.selectBean) {
      this.confirmationService.confirm({
        message: '是否删除选中的数据?',
        accept: () => {
          const param = {
            flowId: this.paginatorBean.selectBean.flowId
          };
          this.managerService.deleteFlow(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
              this.queryList(false);
              this.msgs = [{ severity: 'success', summary: '成功', detail: '删除成功!' }];
            } else {
              this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
          });
        },
        reject: () => {
          this.msgs = [{ severity: 'info', summary: '删除', detail: '你选择了拒绝删除！' }];
        }
      });
    } else {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '请至少选择一条进行删除!' }];
    }
  }
}