import { Component, OnInit } from '@angular/core';

import { Message, SelectItem } from 'primeng/primeng';
import { Router } from '@angular/router';
import { CreditRrmService } from '../../http/credit-rrm.service';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'business-flow-config',
  templateUrl: './business-flow-config.component.html',
  styleUrls: ['./business-flow-config.component.scss']
})

// tslint:disable-next-line:component-class-suffix
export class BusinessFlowConfig implements OnInit {
  [x: string]: any;
  pageNum: any = 1;
  pageSize: any = 10;
  first: number = 0;
  resultCounts: any;
  msgs: Message[] = [];
  isCatelog: boolean = true;
  selectedCheckBox: string[] = [];

  allStageTypes = [];

  businessTypes: SelectItem[];

  unitId: string; // 组件id

  unitName: string; // 组件名称

  productId: string;  // 产品编号

  productName: string; // 产品名称

  businessType: string; // 业务类型

  showProductList = false;

  myform: FormGroup; //校验

  showUnitList = false;

  productList = [];

  selectedProduct: any;

  showSaveBtn = false;

  ngModelValues = new Map<any, any>();

  constructor(private managerService: CreditRrmService, public fb: FormBuilder, public commfunc: Commfunc, public router: Router, ) { }

  ngOnInit() {
    this.getCodeValues();
    this.initValidateItems();
    this.queryAvailableProductList();
  }

  // 获取下拉框值
  getCodeValues() {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    this.businessTypes = codeValues['businessTypes'];
  }

  // 动态构建ngModel属性
  getDynamicNgModelAttr(list) {
    for (const step of list) {
      if (step.list && step.list.length > 0) {
        this.ngModelValues.set(step.stepId, step.stepId);
      }
    }
  }

  // 设置值到ngModel
  setNgMondelValue(event, step) {
    this.ngModelValues.set(step.stepId, event);
  }

  // 获取预选的值
  setPreSelectRedio(array) {
    for (const step of array) {
      if (step.list) {
        for (const item of step.list) {
          if (item.bind === 0) {
            this.ngModelValues.set(step.stepId, '' + item.flowId);
          }
        }
      }
    }
  }

  // 根据筛选条件查询配置历史数据
  queryConfigHistory() {
    this.selectedCheckBox = [];
    if (!this.unitId) {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '请输入功能名称!' }];
      return;
    }
    const queryParam = {
      nodeId: Number(this.unitId),
      productId: this.productId,
      businessType: this.businessType === '' ? undefined : this.businessType
    };
    this.managerService.queryStepFlow(queryParam).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.allStageTypes = data.array;
        this.getDynamicNgModelAttr(data.array);
        this.setPreSelectRedio(data.array);
        this.showSaveBtn = true;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '失败', detail: data.returnCode.message });
      }
    });
  }

  // 查询所有可售产品
  queryAvailableProductList() {
    let dates = {
      pageSize: this.pageSize, // 初始每页条数
      pageNum: this.pageNum, // 初始显示第一页
    }
    this.managerService.queryAvailableProductList(dates).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.productList = data.resultList;
        this.resultCounts = data.total;
      } else {
        this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
      }
    });
  }

  //翻页
  paginate(event) {
    let currentPage = event.page + 1;
    this.pageSize = event.rows;
    this.first = event.page * this.pageSize;
    this.pageNum = currentPage;
    this.queryAvailableProductList();
  }
  // 选择产品
  choosePro() {
    this.showProductList = true;
  }

  // 关闭模态框
  closeSetting1() {
    this.showProductList = false;
  }
  // 选择产品
  // confirmChooseProduct() {

  // if (!this.selectedProduct) {
  //   this.msgs = [{ severity: 'info', summary: '提示', detail: '请选择一条产品数据!' }];
  //   return;
  // }
  // this.productId = this.selectedProduct.productId;
  // this.productName = this.selectedProduct.productName;
  // this.showProductList = false;
  // this.queryConfigHistory();
  // }
  // 选择产品回调
  productCall(event) {
    if (event && event.length > 0) {
      this.productId = event[0].productId;
      this.productName = event[0].productName;
    }
    this.showProductList = false;
    this.queryConfigHistory();
  }
  // 选择功能
  confirmChooseUnit(selectUnit) {
    if (!selectUnit) {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '请选择一条功能数据!' }];
      return;
    }
    this.unitId = selectUnit.nodeId;
    this.unitName = selectUnit.nodeName;
    this.showUnitList = false;
    this.queryConfigHistory();
  }

  // 校验flow是否为本次配置的选中项
  checkFlowBind(flowId) {
    const array = Array.from(this.ngModelValues.values());
    for (const iterator of array) {
      // tslint:disable-next-line:radix
      if (parseInt(iterator) === parseInt(flowId)) {
        return true;
      }
    }
    return false;
  }

  // 保存校验输入数据是否有效
  checkInputFull() {
    if (!this.productId && !this.businessType) {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '产品或者业务类型至少选择一项!' }];
      return false;
    }

    let isSelectFlowList = false; // 本次保存是否有选中流程信息

    for (const step of this.allStageTypes) {
      for (const flow of step.list) {
        if (this.checkFlowBind(flow.flowId)) {
          isSelectFlowList = true;
          break;
        }
      }
      if (isSelectFlowList) break;
    }
    if (!isSelectFlowList) {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '请至少选择一个流程!' }];
      return false;
    }
    return true;
  }

  // 初始化页面每个需要校验的字段的校验项(必输,正则匹配校验)
  initValidateItems() {
    this.myform = this.fb.group({
      'unitName': new FormControl('', Validators.required)
    });
  }

  // 提交之前做一次校验
  preValidateForSubmit() {
    //校验的
    for (const i in this.myform.controls) this.myform.controls[i].markAsDirty();
    //处理是否提交
    for (const i in this.myform.controls) {
      if (!this.myform.controls[i].valid) {
        console.log(this.myform.controls[i]);
        return false;
      }
    }
    return true;
  }

  // 保存配置结果
  save() {
    if (!this.checkInputFull()) return;
    if (!this.preValidateForSubmit()) return;
    const saveSteps = [];
    for (const step of this.allStageTypes) {
      const newStep = { stepId: step.stepId, list: [] };
      for (const flow of step.list) {
        if (this.checkFlowBind(flow.flowId)) {
          newStep.list.push({ id: flow.id, flowId: flow.flowId, bind: 0 });
        } else {
          newStep.list.push({ id: flow.id, flowId: flow.flowId, bind: 1 });
        }
      }
      saveSteps.push(newStep);
    }
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let chName = JSON.parse(this.commfunc.getSessionDataCH("chName"));
    const svaeParams = {
      productId: this.productId,
      productName: this.productName,
      businessType: this.businessType,
      array: saveSteps,
      teller: commonHeader['userId'],
      org: chName.orgName,
    };

    this.managerService.addFlowByProduct(svaeParams).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs.push({ severity: 'success', summary: '成功', detail: '绑定成功!' });
        setTimeout(() => {
          this.router.navigate(['/pages/tzb/custom/credit-rrm/businessFlowConfigQuery']);
        }, 300);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '失败', detail: data.returnCode.message });
      }
    });
  }
}