import { Component, OnInit, Input } from '@angular/core';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { CreditRrmService } from '../../http/credit-rrm.service';
import { RuleBean } from './bean/rule.bean';
import { QueryParamBean } from './bean/query.param.bean';
import { CreditRrmPaginatorBean } from '../../bean/credit-rrm-paginator-bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
    // tslint:disable-next-line:component-selector
    selector: 'unit-node-binding',
    templateUrl: './unit-node-binding.component.html',
    styleUrls: ['./unit-node-binding.component.scss']
})
export class UnitNodeBindingComponent implements OnInit {

    // 提示信息
    msgs: Message[] = [];

    // 查询条件
    queryParamBean = new QueryParamBean();

    // 分页实体对象
    paginatorBean = new CreditRrmPaginatorBean();

    @Input() unitBean: any;

    // 显示引入弹出框
    displayRuleList = false;

    //组件类型
    showUnitTypes = [];

    constructor(
        private router: Router,
        public commfunc: Commfunc,
        private routes: ActivatedRoute,
        private managerService: CreditRrmService
    ) { }

    ngOnInit() {
        this.queryParamBean.nodeId = this.unitBean.nodeId;
        this.getCodeValues();
        this.query(false); // 查询规则列表
    }

    // 获取下拉框值
    getCodeValues() {
        this.commfunc.codeValue();
        const codeValues = this.commfunc.codeDatas;
        this.showUnitTypes = codeValues['showNodeType'];
    }

    // 组件关联规则查询
    query(nextPage) {
        if (!nextPage) {
            this.paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象
        }

        this.queryParamBean.pageNum = this.paginatorBean.currentPage;
        this.queryParamBean.pageSize = this.paginatorBean.pageSize;

        this.managerService.queryRuleByNode(this.queryParamBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.paginatorBean.queryList = data.list;
                this.paginatorBean.resultCounts = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '失败', detail: data.returnCode.message });
            }
        });
    }
    // 分页按钮事件
    paginate(event) {
        this.paginatorBean.currentPage = event.page + 1;
        this.paginatorBean.pageSize = event.rows;
        this.paginatorBean.first = event.page * this.paginatorBean.pageSize;
        this.query(true);
    }


    // 删除
    delete() {
        const ruleUids = [];
        if (this.paginatorBean.selectBean && this.paginatorBean.selectBean.length > 0) {
            for (const rule of this.paginatorBean.selectBean) {
                ruleUids.push({ 'uid': rule.uid });
            }
            const param = {
                nodeId: this.unitBean.nodeId,
                ruleList: ruleUids,
            };
            this.managerService.deleteRuleByNode(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.query(false);
                    this.msgs = [{ severity: 'success', summary: '成功', detail: '删除成功!' }];
                } else {
                    this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
                }
            });
        } else {
            this.msgs = [{ severity: 'error', summary: '错误', detail: '至少选择一条进行删除!' }];
        }
    }

    // 引入
    introduceCallBack(msg) {
        if (msg === 'success') {
            this.msgs = [{ severity: 'success', summary: '成功', detail: '引入成功!' }];
        } else {
            this.msgs = [{ severity: 'error', summary: '失败', detail: msg }];
        }
        this.displayRuleList = false;
        this.query(false);
    }

}
