import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

import { Message, SelectItem } from 'primeng/primeng';

import { CreditRrmService } from '../../http/credit-rrm.service';
import { CreditRrmPaginatorBean } from '../../bean/credit-rrm-paginator-bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'business-flow-config-query',
  templateUrl: './business-flow-config-query.component.html',
  styleUrls: ['./business-flow-config-query.component.scss']
})

// tslint:disable-next-line:component-class-suffix
export class BusinessFlowConfigQuery implements OnInit {

  businessTypes: any;
  msgs: Message[] = [];

  stageTypes: SelectItem[];

  showProductList = false;

  productList = [];

  selectedProduct: any;

  nodeType: string = '0';//功能类型

  nodeId: string;

  unitType = [];  // 组件类型

  paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象

  flages: boolean = false;//判断是否有阶段

  flaged: number = 0;//判断是名称还是ID

  isCatelog: boolean = true;

  // 查询列表参数对象
  queryParam = {
    pageSize: 10,
    pageNum: 1,
    productId: '',
    productName: '',
    stepId: '',
    nodeId: '',
  };

  constructor(private router: Router, private managerService: CreditRrmService, public commfunc: Commfunc, ) { }

  ngOnInit() {
    this.checkfunction();
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    this.unitType = codeValues['unitType'];
    this.businessTypes = codeValues['businessTypes'];
    let businessTypesd = this.businessTypes.splice(0, 1);
    this.queryAvailableProductList();
    this.query(false);
  }

  // 查询列表
  query(nextPage) {
    if (!nextPage) {
      this.paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象
    }
    this.managerService.queryNodeConfList(this.queryParam).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.paginatorBean.queryList = data.list;
        this.paginatorBean.resultCounts = data.totalNum;

      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '失败', detail: data.returnCode.message });
      }
    });
  }

  //重置
  reset() {
    this.queryParam = {
      pageSize: 10,
      pageNum: 1,
      productId: '',
      productName: '',
      stepId: '',
      nodeId: '',
    };
  }

  // 查询所有的阶段列表
  getStageTypes() {
    this.stageTypes = [];
    this.stageTypes.push({ label: '--请选择--', value: '' });
    this.managerService.queryStepList(this.queryParam).subscribe(data => {
      if (data.returnCode.code == 'success') {
        const resultList = data.list;
        if (resultList.length == 0) {
          this.flages = false;
        } else {
          this.flages = true;
          for (const item of resultList) {
            this.stageTypes.push({ label: item.stepName, value: item.stepId });
          }
        }

      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '失败', detail: data.returnCode.message });
      }
    });
  }

  // 查询所有可售产品
  queryAvailableProductList() {
    this.managerService.queryAvailableProductList({}).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.productList = data.resultList;
      } else {
        this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
      }
    });
  }

  //产品名称选择
  showProductLists() {
    this.flaged = 1;
    this.showProductList = true;
  }
  //产品ID选择
  showProductListe() {
    this.flaged = 2;
    this.showProductList = true;
  }
  // 关闭模态框
  closeSetting1() {
    this.showProductList = false;
  }

  // 选择产品回调
  productCall(event) {
    if (event && event.length > 0) {
      if (this.flaged == 1) {
        this.queryParam.productName = event[0].productName;
      } else if (this.flaged == 2) {
        this.queryParam.productId = event[0].productId;
      }
    }
    this.showProductList = false;
  }

  // 分页按钮事件
  paginate(event) {
    this.paginatorBean.currentPage = event.page + 1;
    this.paginatorBean.pageSize = event.rows;
    this.paginatorBean.first = event.page * this.paginatorBean.pageSize;
    this.queryParam.pageNum = this.paginatorBean.currentPage;
    this.query(true);
  }
  //获取nodeId值
  checkfunction() {
    if (this.nodeType) {
      this.flages = false;
      this.stageTypes = [];
      this.stageTypes.push({ label: '--请选择--', value: '' });
      let dates = {
        nodeName: '',
        nodeType: this.nodeType,
        ruleName: '',
        nodeId: '',
        pageSize: 10, // 初始每页条数
        pageNum: 1, // 初始显示第一页
        startIndex: 0, // 分页起始记录行号
      }
      this.managerService.queryNodeList(dates).subscribe(data => {
        if (data.returnCode.code == 'success') {
          this.nodeId = data.list[0].nodeId;
          this.queryParam.nodeId = this.nodeId;
          this.getStageTypes();
        } else {
          this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
        }
      });
    } else {
      this.flages = false;
      this.stageTypes = [];
      this.stageTypes.push({ label: '--请选择--', value: '' });
    }
  }
}