import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { SelectItem, Message, ConfirmationService } from 'primeng/primeng';

import { ParamsBean } from '../bean/params.bean';
import { CreditRrmService } from '../../http/credit-rrm.service';
import { CreditRrmPaginatorBean } from '../../bean/credit-rrm-paginator-bean';
import { QueryNodeBean } from '../bean/query.bean';
import { QueryParamBean } from 'app/pages/tzb/custom/credit-rrm/unit/unit-node-binding/bean/query.param.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'node-list',
  templateUrl: './node-list.component.html',
  styleUrls: ['./node-list.component.scss']
})

export class NodeListComponent implements OnInit {

  // 规则查询页面
  displayRule = false;

  // 定义组件类型下拉框
  unitType: SelectItem[];

  // 提示信息
  msgs: Message[] = [];

  // 规则编号
  ruleUids: any = [];

  selectedUnit: any = [];

  unitBean: any;

  showAddPage = false;

  showUpdatePage = false;

  selectBean: any;

  showUnitTypes = [];

  paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象

  qureyParam = new QueryNodeBean();

  // 构造器
  constructor(
    private router: Router,
    public commfunc: Commfunc,
    private routes: ActivatedRoute,
    private managerService: CreditRrmService,
    private confirmationService: ConfirmationService,
  ) { }

  // 初始化
  ngOnInit() {
    this.getCodeValues();
    this.queryList(false);
  }

  // 获取下拉框值
  getCodeValues() {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    this.showUnitTypes = codeValues['showNodeType'];
  }

  queryList(nextPage) {
    if (!nextPage) {
      this.paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象
    }
    this.qureyParam.pageNum = this.paginatorBean.currentPage;
    this.qureyParam.pageSize = this.paginatorBean.pageSize;
    this.managerService.queryRuleList(this.qureyParam).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.paginatorBean.queryList = data.list;
        this.paginatorBean.resultCounts = data.totalNum;
      } else {
        this.msgs = [{ severity: 'error', summary: '失败', detail: data.returnCode.message }];
      }
    });
  }

  reset() {
    this.qureyParam = new QueryNodeBean();
  }

  // 分页按钮事件
  paginate(event) {
    // tslint:disable-next-line:radix
    this.paginatorBean.currentPage = event.page + 1;
    this.paginatorBean.pageSize = event.rows;
    this.paginatorBean.first = event.page * this.paginatorBean.pageSize;
    this.queryList(true);
  }

  // 删除
  delete() {
    if (this.paginatorBean.selectBean) {
      this.confirmationService.confirm({
        message: '是否删除选中的数据?',
        accept: () => {
          const param = {
            uid: this.paginatorBean.selectBean.uid
          };
          this.managerService.deleteRule(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
              this.queryList(false);
              this.msgs = [{ severity: 'success', summary: '成功', detail: '删除成功!' }];
            } else {
              this.msgs = [{ severity: 'error', summary: '失败', detail: data.returnCode.message }];
            }
          }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
          });
        },
        reject: () => {
          this.msgs = [{ severity: 'info', summary: '删除', detail: '你选择了拒绝删除！' }];
        }
      });
    } else {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '请至少选择一条进行删除!' }];
    }
  }

  // 新增成功
  addUnitSuccessHandel(msg) {
    if (msg === 'success') {
      this.msgs = [{ severity: 'success', summary: '成功', detail: '新增成功!' }];
    } else {
      this.msgs = [{ severity: 'error', summary: '新增失败', detail: msg }];
    }
    this.showAddPage = false;
    this.queryList(false);
  }

  // 修改成功
  updateUnitSuccessHandel(msg) {
    if (msg === 'success') {
      this.msgs = [{ severity: 'success', summary: '成功', detail: '修改成功!' }];
    } else {
      this.msgs = [{ severity: 'error', summary: '失败', detail: msg }];
    }
    this.showUpdatePage = false;
    this.queryList(false);
  }

  // 规则编辑
  editRule() {
    if (this.paginatorBean.selectBean) {
      if (this.paginatorBean.selectBean.ruleType === '0') {
        this.selectBean = this.paginatorBean.selectBean;
        this.showUpdatePage = true;
        return;
      }
      const param = {
        'ruleType': this.paginatorBean.selectBean.ruleType,
        'rulePath': this.paginatorBean.selectBean.rulePath
      };
      // 调用新增接口
      this.managerService.modifyRule(param).subscribe(data => {
        if (data.returnCode.code == 'success') {
          const trnaString = {
            'uid': this.paginatorBean.selectBean.uid,
            'url': data.url
          };
          this.router.navigate(['/pages/tzb/custom/credit-rrm/ruleEdit', JSON.stringify(trnaString)]);
        } else {
          this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
          return;
        }
      });
    } else {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '只能选择一条记录!' }];
    }
  }
}