import { Router, ActivatedRoute } from '@angular/router';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { ParamsBean } from '../bean/params.bean';
import { CreditRrmService } from '../../http/credit-rrm.service';
import { CreditRrmPaginatorBean } from '../../bean/credit-rrm-paginator-bean';
import { Message } from 'primeng/primeng';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'node-dialog',
  templateUrl: './node-dialog.component.html',
  styleUrls: ['./node-dialog.component.scss']
})

export class NodeDialogComponent implements OnInit {

  // 组件查询条件
  paramsBean: ParamsBean = new ParamsBean();

  @Input() unitBean: any;

  @Output() outValue = new EventEmitter();

  @Output() closeDialog = new EventEmitter();

  paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象

  msgs: Message[] = []; // 提示信息

  qureyBean = {
    'ruleName': '',
    'pageSize': 10,
    'pageNum': 1
  };

  // 构造器
  constructor(
    private router: Router,
    private routInfo: ActivatedRoute,
    private managerService: CreditRrmService
  ) { }

  // 初始化
  ngOnInit() {
  }

  // 查询列表
  queryList(nextPage) {
    if (!nextPage) {
      this.paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象
    }
    this.qureyBean.pageNum = this.paginatorBean.currentPage;
    this.qureyBean.pageSize = this.paginatorBean.pageSize;
    this.qureyBean.ruleName = this.paramsBean.ruleName;

    // 调用查询接口
    this.managerService.queryRuleList(this.qureyBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.paginatorBean.queryList = data.list;
        this.paginatorBean.resultCounts = data.totalNum;
      } else {
        this.msgs = [{ severity: 'error', summary: '失败', detail: data.returnCode.message }];
      }
    });
  }

  // 分页按钮事件
  paginate(event) {
    // tslint:disable-next-line:radix
    this.paginatorBean.currentPage = event.page + 1;
    this.paginatorBean.pageSize = event.rows;
    this.paginatorBean.first = event.page * this.paginatorBean.pageSize;
    this.queryList(true);
  }

  // 重置
  reset() {
    this.paramsBean.ruleName = '';
  }

  // 组件关联规则保存
  toConfirm() {
    const ruleDatas = [];
    if (this.paginatorBean.selectBean && this.paginatorBean.selectBean.length > 0) {
      for (const rule of this.paginatorBean.selectBean) {
        if (rule.rulePath == null) {
          ruleDatas.push({
            'uid': rule.uid,
            'ruleName': rule.ruleName,
            'ruleType': rule.ruleType,
            'rulePath': "",
          });
        } else {
          ruleDatas.push({
            'uid': rule.uid,
            'ruleName': rule.ruleName,
            'ruleType': rule.ruleType,
            'rulePath': rule.rulePath,
          });
        }
      }
      const param = {
        nodeId: this.unitBean.nodeId,
        nodeName: this.unitBean.nodeName,
        ruleList: ruleDatas,
      };
      // 调用新增接口
      this.managerService.addRuleByNode(param).subscribe(data => {
        if (data.returnCode.code == 'success') {
          this.outValue.emit('success');
        } else {
          this.outValue.emit(data.returnCode.message);
        }
      });
    } else {
      this.outValue.emit('请至少选择一条数据!');
    }
  }

  // 关闭弹框
  back() {
    this.closeDialog.emit('close');
  }
}
