import { ActivatedRoute } from '@angular/router';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { SelectItem, Message } from 'primeng/primeng';

import { CreditRrmService } from '../../http/credit-rrm.service';
import { CreditRrmPaginatorBean } from '../../bean/credit-rrm-paginator-bean';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'flow-detail',
  templateUrl: './flow-detail.component.html',
  styleUrls: ['./flow-detail.component.scss']
})

export class FlowDetailComponent implements OnInit {

  stageTypes: SelectItem[]; // 阶段类型

  paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象

  @Input() flowPath: string; // 流程路径

  @Output() closeDialog = new EventEmitter(); // 父子组件消息传递

  msgs: Message[] = []; // 消息弹出框

  // 构造器
  constructor(private routInfo: ActivatedRoute, private managerService: CreditRrmService) {
  }

  // 初始化
  ngOnInit() {
    this.queryList();
  }

  // 查询流程下所有规则
  queryList() {
    const params = {
      flowPath: this.flowPath
    };
    this.managerService.queryFlowDetail(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.paginatorBean.queryList = data.list;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '失败', detail: data.returnCode.message });
      }
    });
  }

  // 关闭弹框
  close() {
    this.closeDialog.emit('close');
  }
}