import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Message } from 'primeng/primeng';
import { UnitBean } from './bean/unit.bean';
import { Router } from '@angular/router';
import { CreditRrmService } from '../../http/credit-rrm.service';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'unit-add',
  templateUrl: './unit-add.component.html',
  styleUrls: ['./unit-add.component.scss']
})
export class UnitAddComponent implements OnInit {

  // 定义组件类型下拉框
  unitType = [];

  // 提示信息
  msgs: Message[] = [];

  // 组件信息
  unitBean = new UnitBean();

  @Output() outValue = new EventEmitter(); // 用于将操作结果信息输出到父组件

  @Output() closeDialog = new EventEmitter();

  myform: FormGroup; //校验


  constructor(
    public commfunc: Commfunc,
    private router: Router,
    public fb: FormBuilder,
    private managerService: CreditRrmService
  ) { }

  ngOnInit() {
    this.getCodeValues();
    this.initValidateItems();
    this.unitBean.nodeType = '0';
  }

  // 获取下拉框值
  getCodeValues() {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    this.unitType = codeValues['unitType'];

    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let chName = JSON.parse(this.commfunc.getSessionDataCH("chName"));
    this.unitBean.teller = commonHeader['userId'];
    this.unitBean.org = chName.orgName;
  }

  // 初始化页面每个需要校验的字段的校验项(必输,正则匹配校验)
  initValidateItems() {
    this.myform = this.fb.group({
      // 'nodeType': new FormControl('', Validators.required),
      'nodeName': new FormControl('', [Validators.required, Validators.maxLength(20)]),
      'remark': new FormControl('', [Validators.required, Validators.maxLength(60)]),
      'teller': new FormControl('', [Validators.required]),
      'org': new FormControl('', Validators.required)
    });
  }

  // 提交之前做一次校验
  preValidateForSubmit() {
    //校验的
    for (const i in this.myform.controls) this.myform.controls[i].markAsDirty();
    //处理是否提交
    for (const i in this.myform.controls) {
      if (!this.myform.controls[i].valid) {
        return false;
      }
    }
    return true;
  }


  // 组件保存
  addUnit() {
    if (!this.preValidateForSubmit()) return;
    this.managerService.createNode(this.unitBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.outValue.emit('success');
      } else {
        this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
        // this.closeDialog.emit(data.returnCode.message);
      }
    });
  }

  // 返回
  toBack() {
    this.closeDialog.emit();
  }
}
