import { Component, OnInit, OnDestroy } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import { CreditRrmService } from '../../http/credit-rrm.service';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'rule-edit',
  templateUrl: './rule-edit.component.html',
  styleUrls: ['./rule-edit.component.scss']
})
export class RuleEditComponent implements OnInit, OnDestroy {

  constructor(private sanitizer: DomSanitizer,
    private managerService: CreditRrmService,
    private routInfo: ActivatedRoute,
    private router: Router) {
  }

  editUrl: any;
  routeParams: any;
  ngOnInit() {
    this.routInfo.params.subscribe(data => {
      this.routeParams = JSON.parse(data['transString']);
      let urls = encodeURI(this.routeParams.url);//转换地址中中文名格式
      this.editUrl = this.sanitizer.bypassSecurityTrustResourceUrl(urls);
    });
  }

  ngOnDestroy(): void {
    const param = { uid: this.routeParams.uid };
    // 刷新规则
    this.managerService.refreshRule(param).subscribe(data => {
    })
  }

  back() {
    this.router.navigate(['/pages/tzb/custom/credit-rrm/node']);
  }
}
