import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CreditRrm } from './credit-rrm.component';
import { UnitListComponent } from './unit/unit-list/unit-list.component';
import { UnitMaintenanceComponent } from './unit/unit-maintenance/unit-maintenance.component';
import { NodeListComponent } from './node/node-list/node-list.component';
import { RuleEditComponent } from './rule/rule-edit/rule-edit.component';
import { FlowListComponent } from './flow/flow-list/flow-list.component';
import { RuleDesignComponent } from './rule/rule-design/rule-design.component';
import { BusinessFlowConfig } from './config/business-flow-config/business-flow-config.component';
import { BusinessFlowConfigQuery } from './config/business-flow-config-query/business-flow-config-query.component';
import { OperateLogComponent } from './log/operate-log/operate-log.component';
import { InvokeLogComponent } from './log/invoke-log/invoke-log.component';
import { CheckQualification } from './testInterface/check-qualification/check-qualification.component';
import { QueryQualificationReview } from './testInterface/query-qualification-review/query-qualification-review.component';

export const routes: Routes = [
  {
    path: '',
    component: CreditRrm,
    children: [
      { path: 'unit', component: UnitListComponent },
      { path: 'unit-maintenance/:selectNode/:showPanelIndex', component: UnitMaintenanceComponent, },
      { path: 'node', component: NodeListComponent, },
      { path: 'ruleEdit/:transString', component: RuleEditComponent, },
      { path: 'flowList', component: FlowListComponent, },
      { path: 'ruleDesign/:flowParam', component: RuleDesignComponent, },
      { path: 'businessFlowConfig', component: BusinessFlowConfig, },
      { path: 'businessFlowConfigQuery', component: BusinessFlowConfigQuery, },
      { path: 'operateLog', component: OperateLogComponent, },
      { path: 'invokeLog', component: InvokeLogComponent, },
      { path: 'checkQualification', component: CheckQualification, },
      { path: 'queryQualification', component: QueryQualificationReview}
    ]
  }
];

export const CreditRrmRouting: ModuleWithProviders = RouterModule.forChild(routes);
