import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';

import { Message } from 'primeng/primeng';

import { FlowBean } from '../bean/flow.bean';
import { CreditRrmService } from '../../http/credit-rrm.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'flow-add',
  templateUrl: './flow-add.component.html',
  styleUrls: ['./flow-add.component.scss']
})

export class FlowAddComponent implements OnInit {

  addFlow = new FlowBean();

  @Input() stepNo: string; // 阶段编号

  @Output() outValue = new EventEmitter();

  @Output() closeDialog = new EventEmitter();

  myform: FormGroup; //校验

  stepList = [{ label: '请选择', value: '' }];

  showUnitList = false;

  msgs: Message[] = [];

  unitId: string;

  unitName: string;

  tempStepId: string;

  // 构造器
  constructor(private managerService: CreditRrmService, public fb: FormBuilder, public commfunc: Commfunc) { }

  // 初始加载
  ngOnInit() {
    this.addFlow.stepId = this.stepNo;
    this.initValidateItems();
  }

  // 初始化页面每个需要校验的字段的校验项(必输,正则匹配校验)
  initValidateItems() {
    this.myform = this.fb.group({
      'unitName': new FormControl('', [Validators.required]),
      'tempStepId': new FormControl('', [Validators.required]),
      'flowName': new FormControl('', [Validators.required, Validators.maxLength(20)]),
      'remark': new FormControl('', [Validators.required, Validators.maxLength(60)]),
      'teller': new FormControl('', [Validators.required]),
      'org': new FormControl('', [Validators.required])
    });

    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let chName = JSON.parse(this.commfunc.getSessionDataCH("chName"));
    this.addFlow.teller = commonHeader['userId'];
    this.addFlow.org = chName.orgName;
  }

  // 提交之前做一次校验
  preValidateForSubmit() {
    //校验的
    for (const i in this.myform.controls) this.myform.controls[i].markAsDirty();
    //处理是否提交
    for (const i in this.myform.controls) {
      if (!this.myform.controls[i].valid) {
        return false;
      }
    }
    return true;
  }

  // 保存处理
  save() {
    if (!this.preValidateForSubmit()) return;
    this.managerService.createFlow(this.addFlow).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.outValue.emit('success');
      } else {
        this.outValue.emit(data.returnCode.message);
      }
    });
  }

  close() {
    this.closeDialog.emit('close');
  }

  // 选择一条功能数据回调处理
  confirmChooseUnit(selectUnit) {
    if (!selectUnit) {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '请选择一条功能数据!' }];
      return;
    }
    this.unitId = selectUnit.nodeId;
    this.unitName = selectUnit.nodeName;
    this.showUnitList = false;
    this.queryStepList(this.unitId);
  }

  // 查询阶段列表
  queryStepList(unitId) {
    const param = { nodeId: unitId };
    this.managerService.queryStepList(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        for (const item of data.list) {
          this.stepList.push({ label: item.stepName, value: item.stepId });
        }
      } else {
        this.msgs = [{ severity: 'error', summary: '失败', detail: data.returnCode.message }];
      }
    });
  }

  stepChange() {
    this.addFlow.stepId = this.tempStepId;
  }
}
