import { Component, OnInit } from '@angular/core';

import { Message } from 'primeng/primeng';

import { CreditRrmService } from '../../http/credit-rrm.service';
import { CreditRrmPaginatorBean } from '../../bean/credit-rrm-paginator-bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'app-invoke-log',
  templateUrl: './invoke-log.component.html',
  styleUrls: ['./invoke-log.component.scss']
})

export class InvokeLogComponent implements OnInit {

  detailContent: string;

  showDetailDialog = false;

  requestMsgContent: string;

  showRequestMsgDialog = false;

  queryParam = {
    service: '',
    pageSize: 10,
    pageNum: 1
  };

  successList = [];

  msgs: Message[] = [];

  paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象

  // 构造器
  constructor(public commfunc: Commfunc, private managerService: CreditRrmService) { }

  // 初始化
  ngOnInit() {
    this.getCodeValues();
    this.query(false);
  }

  // 获取下拉框值
  getCodeValues() {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    this.successList = codeValues['invokeResult'];
  }

  query(nextPage) {
    if (!nextPage) {
      this.paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象
    }
    this.queryParam.pageNum = this.paginatorBean.currentPage;
    this.queryParam.pageSize = this.paginatorBean.pageSize;
    this.managerService.queryInvokLog(this.queryParam).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.paginatorBean.queryList = data.list;
        this.paginatorBean.resultCounts = data.totalNum;
      } else {
        this.msgs = [{ severity: 'error', summary: '失败', detail: data.returnCode.message }];
      }
    });
  }

  // 重置
  reset() {
    this.queryParam = {
      service: '',
      pageSize: 10,
      pageNum: 1
    };
  }

  // 分页按钮事件
  paginate(event) {
    // tslint:disable-next-line:radix
    this.paginatorBean.currentPage = event.page + 1;
    this.paginatorBean.pageSize = event.rows;
    this.paginatorBean.first = event.page * this.paginatorBean.pageSize;
    this.query(true);
  }

  // 显示执行详情
  showDetail(item) {
    this.detailContent = item.detail;
    this.showDetailDialog = true;
  }

  // 显示请求报文详情
  showReustMsgDetail(item) {
    this.requestMsgContent = item.request;
    this.showRequestMsgDialog = true;
  }
}