import { Component, OnInit, Input } from '@angular/core';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { CreditRrmService } from '../../http/credit-rrm.service';
import { CreditRrmPaginatorBean } from '../../bean/credit-rrm-paginator-bean';
import { StepQueryBean } from './bean/step.query.bean';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'step-list',
  templateUrl: './step-list.component.html',
  styleUrls: ['./step-list.component.scss']
})
export class StepListComponent implements OnInit {


  queryParam = new StepQueryBean();  // 查询列表请求参数

  paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象

  msgs: Message[] = [];

  stageTypes = []; // 阶段类型

  showAddStagePage = false;
  showEditStagePage = false;


  @Input() unitBean: any;


  constructor(private router: Router,
    private managerService: CreditRrmService,
    private confirmationService: ConfirmationService,
    private routInfo: ActivatedRoute) {
  }

  ngOnInit() {
    this.queryList(false);
  }

  // 查询列表
  queryList(nextPage) {
    if (!nextPage) {
      this.paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象
    }
    const param = { 'nodeId': this.unitBean.nodeId };
    this.managerService.queryStepList(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.paginatorBean.queryList = data.list;
        this.paginatorBean.resultCounts = data.totalNum;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '失败', detail: data.returnCode.message });
      }
    });

  }

  // 分页按钮事件
  paginate(event) {
    // tslint:disable-next-line:radix
    this.paginatorBean.currentPage = event.page + 1;
    this.paginatorBean.pageSize = event.rows;
    this.paginatorBean.first = event.page * this.paginatorBean.pageSize;
    this.queryList(true);
  }

  // 新增成功处理函数
  addStageCallBack(msg) {
    if (msg === 'success') {
      this.msgs = [{ severity: 'success', summary: '成功', detail: '新增阶段成功!' }];
    } else {
      this.msgs = [{ severity: 'error', summary: '失败', detail: msg }];
    }
    this.showAddStagePage = false;
    this.queryList(false);
  }

  // 编辑
  edit() {
    if (this.paginatorBean.selectBean) {
      this.showEditStagePage = true;
    } else {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '至少选择一条进行编辑!' }];
    }
  }


  // 删除
  delete() {
    if (this.paginatorBean.selectBean) {
      this.confirmationService.confirm({
        message: '是否删除选中的数据?',
        accept: () => {
          const param = {
            stepId: this.paginatorBean.selectBean.stepId,
            stepName: this.paginatorBean.selectBean.stepName
          };
          this.managerService.deleteStep(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
              this.queryList(false);
              this.msgs = [{ severity: 'success', summary: '成功', detail: '删除成功!' }];
            } else {
              this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
          });
        },
        reject: () => {
          this.msgs = [{ severity: 'info', summary: '删除', detail: '你选择了拒绝删除！' }];
        }
      });
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'info', summary: '提示', detail: '至少选择一条进行删除!' });
    }
  }



  // 修改成功处理函数
  editStageCallBack(msg) {
    if (msg === 'success') {
      this.msgs = [{ severity: 'success', summary: '成功', detail: '修改阶段成功!' }];
    } else {
      this.msgs = [{ severity: 'error', summary: '失败', detail: msg }];
    }
    this.showEditStagePage = false;
    this.queryList(false);
  }

}
