import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';

import { Message } from 'primeng/primeng';

import { CreditRrmService } from '../../http/credit-rrm.service';
import { FormGroup } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'node-update',
  templateUrl: './node-update.component.html',
  styleUrls: ['./node-update.component.scss']
})

export class NodeUpdateComponent implements OnInit {

  // tslint:disable-next-line:no-input-rename
  @Input('inputParams') selectRule;

  @Output() closeDialog = new EventEmitter();

  @Output() updateSuccess = new EventEmitter();

  msgs: Message[] = [];

  showUnitTypes = [];

  queryAttrList = []; // 用于页面动态显示输入属性列表

  saved = false;

  ngModelValues = new Map<any, any>();

  constructor(private managerService: CreditRrmService, public commfunc: Commfunc) { }

  ngOnInit() {
    this.getCodeValues();
    this.queryDynamicAttrList();
  }

  // 获取可编辑属性列表
  queryDynamicAttrList() {
    const param = { uid: this.selectRule.uid };
    this.managerService.queryParamByUid(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        if (data.data.param.length > 0) {
          this.saved = true;
          const result = data.data.param;
          this.getngModels(result);
        }
      } else {
        this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
      }
    });
  }

  getngModels(result) {
    for (const item of result) {
      this.ngModelValues.set(item.name, item.value);
      if (!item.length) {
        item.length = 200;
      }
      this.queryAttrList.push(item);
    }
  }


  // 设置值到ngModel
  setNgMondelValue(event, name) {
    this.ngModelValues.set(name, event);
  }

  // 保存
  save() {
    for (const item of this.queryAttrList) {
      item.value = this.ngModelValues.get(item.name);
    }
    const saveParam = {
      uid: this.selectRule.uid,
      param: JSON.stringify(this.queryAttrList)
    };
    this.managerService.confBeanField(saveParam).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [{ severity: 'success', summary: '成功', detail: '修改成功！' }];
        this.updateSuccess.emit('success');
      } else {
        this.updateSuccess.emit(data.returnCode.message);
        this.msgs = [{ severity: 'error', summary: '失败', detail: '修改失败！' }];
      }
    });
  }

  // 获取下拉框值
  getCodeValues() {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    this.showUnitTypes = codeValues['showNodeType'];
  }

  close() {
    this.closeDialog.emit('close');
  }
}