export class CheckQualificationBean {
    processId: string;	// 交易流水号
    tranSeqNo: string;	// 	业务流水号
    stageType: string = '0';	// 	阶段类型编码 0：准入筛选 1：预筛选 2：信审评分
    businessLines: string;	// 	业务种类 01供应链、02直销银行、03信用卡
    creditProduct: string = '100000';	// 	授信产品
    sourceSystem: string;	// 	来源系统
    triggerType: string;	// 	触发类型 01:手工触发，02:系统触发
    identityIsTrue: string;	// 	是否身份验真 0-是 1-否
    reason: string;	// 	资质审查原因
    org: string;	// 	交易机构
    teller: string;	// 	交易柜员
    userInfoArray: any;	// 	用户信息组
    // customerNames: string;

    doTest = '1'; // 是否仿真测试(1:是,0:否)
}

export class CustomerBean {
    custType: string;	// 客户类型 01-借款人 02-共同借款人 03-担保人
    custNo: string;	// 客户号
    custName: string;	// 客户名称
    globalType: string;	// 证件类型
    telphoneNo: string;	// 手机号
    acountNo: string;	// 银行卡号
    globalId: string;	// 证件号码
}


export class CheckQualifactionBean {
    dateTime: string;	// 交易时间
    result: string;	// 是否通过
    lastScore: string;	// 风险评估总分
    sysAdvice: string;	// 建议额度
    riskCode: string;	// 风险评级
    investType: string;	// 调查方式
    reportType: string;	// 报表编制
    lossLevel: string;	// 流失等级
    loanAdvice: string;	// 贷款额度
    loanRate: string;	// 贷款利率
    evalArray: any; // 资质审查返回结果
}

export class QualifactionreviewBean {
    qualificationReviewId: string;	// 资质审查编号
    tranSeqNo: string;	// 身份验真渠道
    processId: string;	// 交易流水
    custNo: string;	// 客户号
    custName: string;	// 客户名称
    advocateManagePerson: string;	// 主联系人
    custType: string;	// 客户类型
    cardNo: string;	// 证件号
    cardType: string;	// 证件类型
    telephoneNo: string;	// 手机号
    acountNo: string;	// 银行卡号
    isCreditAuthorized: string;	// 是否征信授权
    stageType: string;	// 阶段类型
    creditProduct: string;	// 授信产品
    creditQuota: string;	// 授信额度
    creditTerm: string;	// 授信期限
    guaranteeQuota: string;	// 担保额度
    guaranteeType: string;	// 担保方式
    sourceSystem: string;	// 客户来源
    tellerNo: string;	// 经办柜员
    tellerOrg: string;	// 经办机构
    qualificationReviewResult: string;	// 资质审查结果
    qualificationReviewFailCode: string;	// 资质审查失败节点
    qualificationReviewReason: string;	// 资质审查失败原因
    creidtValue: string;	// 信用评分
    creidtLevel: string;	// 信用评级
    passFlag: string;	// 评分评级通过标识
    passRate: string;	// 建议通过率
    overPercent: string;	// 可能逾期率
    adviceQuota: string;	// 建议额度
    adviceRate: string;	// 建议利率
    loanQuota: string;	// 定价额度
    loanRate: string;	// 定价利率
    lossLevel: string;	// 流失等级
    investType: string;	// 调查方式
    reportType: string;	// 报表编制
    reportId: string;	// 报表编号
    truthResult: string;	// 身份验真结果(0-不通过，1-通过)
    creditResult: string;	// 征信查询结果(0-无征信记录  1-有征信记录)
    isName: string;	// 是否在名单中记录(0-不在  1-存在)
}