import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { SelectItem, Message } from 'primeng/primeng';
import { CreditRrmService } from '../../http/credit-rrm.service';
import { ParamsBean } from './bean/params.bean';
import { CreditRrmPaginatorBean } from 'app/pages/tzb/custom/credit-rrm/bean/credit-rrm-paginator-bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'unit-dialog',
  templateUrl: './unit-dialog.component.html',
  styleUrls: ['./unit-dialog.component.scss']
})
export class UnitDialogComponent implements OnInit {

  // 组件类型
  unitType: SelectItem[];

  msgs: Message[] = [];

  // 组件查询条件
  paramsBean = new ParamsBean();

  paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象

  // 提示信息

  @Output() outValue = new EventEmitter();

  @Output() closeDialog = new EventEmitter();

  constructor(public commfunc: Commfunc, private managerService: CreditRrmService) { }

  ngOnInit() {
    this.getCodeValues();
    this.query(false);
  }

  // 获取下拉框值
  getCodeValues() {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    this.unitType = codeValues['unitType'];
  }

  // 查询
  query(nextPage) {
    if (!nextPage) {
      this.paginatorBean = new CreditRrmPaginatorBean(); // 分页实体对象
    }
    this.paramsBean.pageNum = this.paginatorBean.currentPage;
    this.paramsBean.pageSize = this.paginatorBean.pageSize;
    this.managerService.queryNodeList(this.paramsBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.paginatorBean.queryList = data.list;
        this.paginatorBean.resultCounts = data.totalNum;
      } else {
        this.outValue.emit(data.returnCode.message);
      }
    });
  }

  // 分页按钮事件
  paginate(event) {
    this.paginatorBean.currentPage = event.page + 1;
    this.paginatorBean.pageSize = event.rows;
    this.paginatorBean.first = event.first;
    this.query(true);
  }

  // 重置
  reset() {
    this.paramsBean = new ParamsBean();
  }

  // 确定选中一条数据
  confirmSelect() {
    if (!this.paginatorBean.selectBean || this.paginatorBean.selectBean.length < 1) {
      this.msgs = [{ severity: 'info', summary: '提示', detail: '请选择一条功能数据!' }];
      return;
    }
    this.outValue.emit(this.paginatorBean.selectBean);
  }

  close() {
    this.closeDialog.emit('close');
  }

}
